@import url("https://fonts.googleapis.com/css?family=Lato&display=swap");
body {
  margin: 0;
  font-family: "Lato", sans-serif;
  background: #fff; }
  body.dark {
    background: #34495e; }
    body.dark .nav,
    body.dark .content {
      background: #34495e; }
    body.dark .nav {
      border-bottom: 2px solid #3498db; }
    body.dark h2,
    body.dark label,
    body.dark p,
    body.dark .icon:before {
      color: #fff; }
    body.dark .icon:before {
      color: #3498db; }
    body.dark hr {
      border: 1px solid #fff; }

* {
  box-sizing: border-box; }

h1,
h2,
h3,
h4,
h5,
h6,
label,
p {
  margin: 0;
  color: #3c3c3c; }

input,
select,
textarea {
  border: 1px solid #6e6c6c;
  outline: none;
  width: 100%;
  max-width: 310px;
  margin: 8px auto;
  padding: 8px 15px;
  text-transform: uppercase;
  vertical-align: middle; }

input:read-only {
  background: #dddddd4f; }

button {
  background: #55acee;
  padding: 10px;
  border: 0;
  font-weight: bold;
  width: 100%;
  color: #fff;
  cursor: pointer; }
  @media (min-width: 992px) {
    button {
      margin-top: 10px; } }
  button:hover {
    background: #6fc6ff; }
  button:active, button:focus {
    outline: none; }

hr {
  border: 1px solid #3c3c3c; }
  @media (min-width: 768px) {
    hr {
      margin-bottom: 20px; } }

@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon.eot");
  src: url("../fonts/icomoon.eot#iefix") format("embedded-opentype"), url("../fonts/icomoon.ttf") format("truetype"), url("../fonts/icomoon.woff") format("woff"), url("../fonts/icomoon.svg#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block; }

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.icon-observacion:before {
  content: "\e926"; }

.icon-destino:before {
  content: "\e947";
  color: #e74c3c; }

.icon-estado:before {
  content: "\e986";
  color: #34495e; }

.icon-origen:before {
  content: "\e9af";
  color: #34495e; }

.icon-tracking:before {
  content: "\e9b8";
  color: #34495e; }

.icon-aviso:before {
  content: "\e9b9";
  color: #34495e; }

.icon-tipo:before {
  content: "\e9bb";
  color: #34495e; }

.icon-check:before {
  content: "\ea11"; }

header {
  position: relative; }
  header .icon {
    color: #3c3c3c; }
    header .icon span {
      font-family: "Lato", sans-serif; }

.nav {
  box-shadow: 1px 3px 5px 0 rgba(0, 0, 0, 0.16);
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  position: fixed;
  width: 100%; }
  .nav__left {
    display: flex;
    align-items: center; }
  .nav__right {
    display: flex;
    align-items: center; }
  .nav__color-mode img {
    display: inline-block;
    vertical-align: middle;
    width: 30px; }
  .nav__color-mode p {
    display: inline-block;
    margin-left: 5px;
    vertical-align: middle; }
  .nav__icon .icon {
    font-size: 24px; }
  .nav__logo img {
    height: 45px; }
  .nav__title p {
    margin-top: -5px;
    font-size: 20px; }

.content {
  padding: 60px 35px 50px;
  max-width: 375px;
  margin: 0 auto; }
  @media (min-width: 768px) {
    .content {
      max-width: 700px; } }
  .content h2 {
    font-size: 20px;
    text-align: center;
    margin: 15px 0; }
  .content .searchResults {
    margin-top: 30px; }

@media (min-width: 768px) {
  form .form-group.year {
    display: inline-block;
    width: 20%;
    margin-right: 15px; } }

@media (min-width: 768px) {
  form .form-group.tracking {
    display: inline-block;
    width: 50%;
    margin-right: 15px; } }

form .form-group.cta {
  margin-top: 15px; }
  @media (min-width: 768px) {
    form .form-group.cta {
      display: inline-block;
      width: 20%;
      margin-top: 0; } }

form .form-group label,
form .form-group input {
  display: block; }
  @media (min-width: 768px) {
    form .form-group label,
    form .form-group input {
      display: inline-block; } }

.search-results {
  padding-top: 30px; }
  @media (min-width: 768px) {
    .search-results .item {
      text-align: center; } }
  .search-results .item__feature {
    display: flex;
    align-items: center;
    padding: 10px 0; }
    @media (min-width: 768px) {
      .search-results .item__feature {
        display: inline-block;
        width: 170px;
        text-align: left; } }
    .search-results .item__feature .icon {
      margin-right: 10px; }
  .search-results .item textarea {
    width: 100%;
    resize: none; }
    @media (min-width: 768px) {
      .search-results .item textarea {
        display: inline-block; } }

.history__date {
  padding: 10px 0; }
  @media (min-width: 768px) {
    .history__date {
      display: inline-block;
      min-width: 250px;
      text-align: center; } }
  .history__date .icon {
    color: #2ecc71;
    margin-right: 10px; }

@media (min-width: 768px) {
  .history__description {
    display: inline-block;
    vertical-align: middle;
    width: 50%; } }

.history__description textarea {
  resize: none;
  width: 100%;
  min-height: 80px; }
